	module.exports = {"data":{"markdownRemark":{"html":"<p>あぁ、私のお気に入り… お前は私のお気に入りだったよ… 昨日はお前をピカピカになるまで研きあげて 魔力もフル充填してあげたんだぜ… お前、なんでそんなに粉々になっちまったんだ …信じらんねえよ ミニ八卦炉よ、お前は香霖特注の最高傑作だった、私の誇りだぜ… 座ってろよクソアマ！次動いたらボコボコにするぞ！ （う、うん！） そこいらの野次馬がやってくるぞ これからは私がやることをあいつらには見せたくねえんだ 泣くのはやめろよ、シロアカ お前を見てると怒鳴りたくなるんだよ なんでそんなこそこそと私のミニ八卦炉を壊したんだよ！ あー、どうしちまったんだよ？ Reimu、私がうるさすぎるか？ 残念だな、くそアマ！ 私が言うことを今回はしっかり聞いてもらうぜ！ 最初はお前怒って弾幕勝負になるんならそれでいいと思ってた でもよ、こっそり私のミニ八卦炉をぶっ壊すなんて お前頭がどうかしちまったのか？ この箒！魔導書！この家全てが私のものだ！ なぜこいつを簡単に壊せたりできるんだ！？ いいか Reimu、お前のライバルを見てみろ！ （いや！） 私を真っ直ぐ見ろと言ってるんだ！！ 私はもう弾幕勝負の相手ですらないってことか？タカピーめ！ （なんでこんなことするのよ！） 黙れ！クソが！！ （あんた酔ってるのよ！こんなことして良いと思ってるの！？） 別に私は何も構いやしないぜ！ さあ、ドライブへ行くんだ！ （いや！） 前に座れよ！ （神社を留守にできないわ！もし妖怪に住み着かれたらどうするの！） すぐに戻るさ、もっともお前は 箒の後ろに吊されて戻ってくるだろうがな －これで終わりだシロアカ、酷い仕打ちをしてくれたもんだ 耐えられないんだ、お前なしで生きるなんて－ ホント酷いことをしてくれたな、Reimu 今回はマジで私もキレたぜ 私の悪戯がこんな形で返ってくるなんて思ってもみなかった でも私が悪戯で陰陽玉を壊した時、私達はまだ若かった 私はまだ13歳だったし　結構昔の話だろ 私達はそれから正々堂々とやりあうつもりだったのに もう最悪だな！ （ホント悪かったわ） ああ…頭の中がぐるぐるしてるぜ （だから弾幕ごっこしましょ） 何すんだよ！ 突然弾幕撃つんじゃねぇ！ お前のせいでちっとも正々堂々じゃねえじゃないかよ これが逝かれたジョークだとでも思ってるのか？ （いや・・・） お前は喉元を切られて死ぬんだよ ハッハー、冗談言ってるとでも思ったか？ 私なんてアウトオブ眼中なんだろ？ 嘘ついてんじゃねえよ、シロアカ！ 目の前に居る私はお前の一体何だって言うんだよ！ クソったれが！ Reimu！ REEIIMU！！ なぜこんなことをしたんだよ！ 私が…酷いと思ってんだろ・・・私が酷いと思ってるんだ… 私から離れろよ…私に触るな！ お前なんて嫌いだ！大嫌いだ！！ 神に誓ってお前が憎いぜ！！！ （グスン・・・） ちきしょう…こんなに仲良くしてるのに どうして私にこんな仕打ちができるんだよ？ （ごめんね…） どうして私にこんな仕打ちができるんだよぉ！！？ －これで終わりだシロアカ、酷い仕打ちをしてくれたもんだ 耐えられないんだ、お前なしで生きるなんて－ さあ降りろよ （いや！怖いわ） 降りろと言っただろ！くそアマめ！ （髪をつかまないで！） （こんなことしないで、お願い、今回はホントに悪かったと思ったから…） （次期主人公の座を譲ってもいいから） クソが！余計なお世話だぜ！ お前がやったことだ、お前がいけないんだろう！ ちきしょう、笑いが止まらないわ…しっかりしようぜMarisssa！ ヘイ、白玉楼の宴会に行った時のことお前覚えてるか？ お前珍しく酔っ払って周りのモノ滅茶苦茶に壊してたな ありゃおかしかったなぁ？ （あぁ・・・） ありゃあおかしかったよなぁ！！ （ああ！） お前とお前の友達が喧嘩して 片方が包丁を持ち出しもみ合ってるうちに、誤って喉元を切ってしまった こんなことが起こってる間に 雑魚妖怪がやって来て　神社に入ってきた 彼女はお前の死体を見付け、死体を食いあさった 何の記録も残らず、そして誰もいなくなった （なんてこと・・・） お前の態度がおかしくなった時に気づくべきだったんだ 私達は・・・ヘイ、どこ行くんだよ、戻ってこいよ！ 私からは、逃げられないんだぜ！ Reimu、ここには、2人しか、いないんだ！ 他には、誰も、いない！ そんなこと、してると！もっと、自分に、不利になるぜ！ …ハッハー、捕まえたぜ！ （きゃぁあ！） さあ叫べよ！ 私も一緒に叫んでやろうか あぁあ、誰か助けてぇぇぇぇえ！！！ 解らねえのか？叫んでも誰にも聞こえないんだ！ 黙って私がこれからやることを受けとめろ！ （いやぁぁぁ！） お前は私の最高の友達だったのにぃ！ （うっ…ガハァッ・・・！） ざあ血を吐けよ！シロアカ！血を流せ！ 血をぶちまけろぉ！！ぶぅちまけろぉぉお！！ －これで終わりだシロアカ、酷い仕打ちをしてくれたもんだ 耐えられないんだ、お前なしで生きるなんて－</p>","frontmatter":{"cover":null,"date":"2004-08-31T00:00:00+09:00","strDate":"2004/8/30","title":"本文"}}},"pathContext":{"path":"/ja/blog/2004/08/31-本文-2/","langKey":"ja"}}

